import Vue from "vue";
import Router from "vue-router";
Vue.use(Router);

export default new Router({
  routes: [{
    path: "/",
    name: 'farm',
    component: () => import("@/view/LP/index.vue"),
  },
  {
    path: "/NFT",
    component: () => import("@/view/NFT/index.vue"),
  },
  {
    path: "/Me",
    component: () => import("@/view/Me/index.vue"),
  },
  {
    path: "/rules",
    component: () => import("@/view/rules/index.vue"),
  },
  {
    path: "/SynthesisDetails",
    meta: {
      title: '合成详情'
    },
    component: () => import("@/view/SynthesisDetails/index.vue"),
  },
  {
    path: "/edit",
    meta: {
      title: '编辑社区名称'
    },
    component: () => import("@/view/editMyCommunity/index.vue"),
  },
  {
    path: "/invitation",
    meta: {
      title: '分享好友'
    },
    component: () => import("@/view/invitation/index.vue"),
  },
  {
    path: "/userDestroy",
    meta: {
      title: 'meta.userDestroy'
    },
    component: () => import("@/view/userDestroy/index.vue"),
  },
  {
    path: "/NFTrecords",
    meta: {
      title: 'meta.NFTrecords'
    },
    component: () => import("@/view/NFTrecords/index.vue"),
  },
  {
    path: "*",
    redirect: "/",
  },
  ],
  mode: 'hash',
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  }
});
