<template>
  <div id="app">
    <Heads></Heads>
    <router-view></router-view>
    <van-tabbar route
                fixed
                placeholder
                border
                v-if="show">
      <van-tabbar-item replace
                       to="/"
                       icon="home-o">
        <template #icon="props">
          <img class="tabbarIcon"
               :src="props.active ? require('@/assets/tab1_active.png') : require('@/assets/tab1.png')" />
        </template>
        {{ $t('lp') }}
      </van-tabbar-item>
      <van-tabbar-item replace
                       to="/NFT"
                       icon="search">
        <template #icon="props">
          <img class="tabbarIcon"
               :src="props.active ? require('@/assets/tab2_active.png') : require('@/assets/tab2.png')" />
        </template>
        {{$t('NFT')}}
      </van-tabbar-item>
      <van-tabbar-item replace
                       to="/Me"
                       icon="search">
        <template #icon="props">
          <img class="tabbarIconMe"
               :src="props.active ? require('@/assets/tab3_active.png') : require('@/assets/tab3.png')" />
        </template>
        {{ $t('me') }}
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import Heads from './components/head'
export default {
  name: 'App',
  components: {
    Heads,
  },
  data () {
    return {
      show: true,
    }
  },
  watch: {
    $route (to, from) {
      let showList = ['/', '/NFT', '/Me']
      if (showList.includes(to.path)) {
        this.show = true
      } else {
        this.show = false
      }
    },
  }
}
</script>

<style>
#app {
  background-color: #f6f7fb;
  position: relative;
  min-height: 100vh;
}
img {
  width: 100%;
  height: 100%;
  display: block;
}
.tabbarIcon {
  width: 23px !important;
  height: 23px !important;
  margin-bottom: 1px;
}
.tabbarIconMe {
  width: 19px !important;
  height: 22px !important;
  margin-bottom: 1px;
}
</style>
