<template>
  <div class="heads">
    <van-notice-bar wrapable
                    :scrollable="false"
                    :class="alert ? 'none' : ''"
                    mode="closeable"
                    color="#909CBA"
                    background="#ECF0F8"
                    :text="$t('warning')">
      <template #right-icon>
        <div class="close"
             @click="alert = true">
          <img src="@/assets/close.png">
        </div>
      </template>
    </van-notice-bar>
    <van-nav-bar :title="$t(title)"
                 v-if="title"
                 left-arrow
                 @click-left="onClickLeft">
    </van-nav-bar>
    <div class="tools">
      <div class="star">
        <img src="@/assets/head_Star.png">
      </div>
      <div class="language">
        <div class="box"
             @click="changeLang">
          <div class="calc">
            <img src="@/assets/head_language.png">
          </div>
          <div class="text">{{nowLanguage}}</div>
          <div class="down">
            <img src="@/assets/head_down.png">
          </div>
        </div>
        <div class="btn"
             @click="content">{{address ? address : 'Content'}}</div>
      </div>
    </div>
    <van-action-sheet v-model="show"
                      :actions="actions"
                      @select="onSelect"
                      :cancel-text="$t('cancel')"
                      close-on-click-action />
  </div>
</template>

<script>
import { vantLocales } from "@/lang";
export default {
  name: 'heads',
  data () {
    return {
      show: false,
      alert: false,
      WEB: null,
      nowLanguage: 'EN',
      title: '',
      actions: [
        // {
        // 	name: '简体中文',
        // 	value: 'zh',
        // },
        {
          name: '繁體中文',
          value: 'hk',
        },
        {
          name: 'English',
          value: 'en',
        },
        // {
        // 	name: 'ViệtName',
        // 	value: 'vi',
        // }
      ],
    }
  },
  watch: {
    $route (to, from) {
      if (to.meta.title) {
        this.title = to.meta.title
      } else {
        this.title = ''
      }
    },
  },
  computed: {
    address () {
      let formatAddress = this.$util.plusXing(this.$store.state.address || '', 3, 4, '.', 3)
      return formatAddress
    }
  },
  mounted () {
    if (localStorage.getItem('language')) {
      this.nowLanguage = localStorage.getItem('language').toUpperCase()
    }
  },
  methods: {
    content () {
    },
    onClickLeft () {
      this.$router.go(-1)
    },
    onClickRight () { },
    onSelect (item) {
      this.$i18n.locale = item.value
      vantLocales(item.value)
      localStorage.setItem('language', item.value)
      this.nowLanguage = item.value.toUpperCase()
    },
    changeLang () {
      this.show = true
    },
  }
}
</script>

<style scoped lang="less">
img {
  width: 100%;
  height: 100%;
  display: block;
}

.none {
  display: none;
}

.close {
  width: 17px;
  height: 17px;
}

.heads {
  position: relative;
  background: #fff;
  z-index: 8;
  /deep/.van-nav-bar__content {
    height: 44px;
  }
}

.tools {
  background: #fff;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 32px;
  padding-right: 25px;
  height: 44px;
  justify-content: space-between;
  .language {
    display: flex;
    flex-direction: row;
    align-items: center;

    .box {
      display: flex;
      flex-direction: row;
      align-items: center;
    }

    .btn {
      font-size: 14px;
      font-family: PingFangSC;
      font-weight: 400;
      color: #ffffff;
      height: 32px;
      width: 80px;
      line-height: 32px;
      text-align: center;
      background: #0076dd;
      margin-left: 20px;
      border-radius: 3px;
    }

    .down {
      width: 6px;
      height: 5px;
    }

    .text {
      font-size: 14px;
      font-weight: 500;
      color: #1f75d9;
      margin: 0 5px;
    }

    .calc {
      height: 18px;
      width: 18px;
    }
  }

  .star {
    height: 28px;
    width: 32px;
  }
}
</style>
